import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core'; 

import { DetailBillComponent } from './detail-bill/detail-bill.component';
import { SellCardComponent } from './sell-card.component';
import { BillType50Module } from 'src/app/cafe/components/bill-type/bill-type-50/bill-type-50.module';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';



const routes: Routes = [
  {
    path: '',
    component: SellCardComponent
  },
  {
    path: 'detail',
    component: DetailBillComponent
  },
]



@NgModule({
  declarations: [
    SellCardComponent, DetailBillComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule, 
    BillType50Module
  ]
})
export class SellCardModule { }
